body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

/* Common css */
.tab-nav-bar {
  position:absolute;
  bottom:0;
  width:100%;
  height:50px;   /* Height of the footer */
}

.tab-nav-bar .tab-icon{
  font-size: 20px;
}

.tab-nav-bar .tab-icon.selected{
  color: rgb(51, 163, 244);
}

.component-header {
  height: 30px;
  line-height: 40px;
  padding: 5px;
}
.component-header > div:first-child{
  float: left;
}

.component-header > div:last-child{
  float: right;
}
.component-header + div {
  clear: both;
}
.component-header > div:last-child.clickable {
  cursor: pointer;
}

.text-center {
  text-align: center;
}
.with-nav-bar .am-list-header {
  padding: 0;
}
/*common css end */


.form-error-msg {
  color: red;
}

/* Baby profile page */
 .baby-avatar {
  height: 100px;
}

 .am-list-content {
  height: 100%;
}

/* End baby profile page */

/* Dashboard page start */
a[role=button] span {
  font-size: 20px;
}
/* End dashboard page */

/* Start habbits page */
.habbits .am-navbar-right {
  font-size: 25px;
}
/* End habbits page */

/* Add Habbit record page start */
.add-habbit-record .habbit-desc,
.add-habbit-record .score-action
{
  padding: 10px;
}
/* Add Habbit record page end */
